import {mapMutations} from "vuex"
    export default {
        props: ['item'],
        methods: {
            ...mapMutations(['removeItem']),
            remove(id) {
                 if(confirm("Do you really want to delete?")){
                       this.removeItem(id) 
                    }
                },
    }
    }
